    return (
      <div class="product">
        <ul class="product-list product-list-recommend">
          <h3 class="product-card-header">附近热卖推荐</h3>
          {this.cardList.map((item) => {
            return (
              <li class="product-card-wrapper">
                <product-card></product-card>
              </li>
            )
          })}
        </ul>
        <ul class="product-list product-list-find">
          <h3 class="product-card-header">附近热卖推荐</h3>
